html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: sans-serif;
  background-color: rgb(230, 230, 230);
  color: #666;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

button, select {
  color: inherit;
}

.container {
  box-sizing: border-box;
  padding: 2em;
  height: 100%;
}

.grid {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: 200px 1fr;
}

h1 {
  font-weight: normal;
}

canvas {
  width: 100%;
  height: 100%;
}

.control-row {
  margin: 1em 0;
}

.controls {
  padding-right: 2em;
}

.control {
  display: block;
  margin: 0.5em 0;
}

.control label {
  display: block;
  font-size: 0.75rem;
}

.control input, button, select {
  width: 100%;
}

